import React from 'react';
import Section from '../Utils/Section';
import Title from '../Utils/Title';
import Text from '../Utils/Text';
import bg1 from '../../assets/img/3ds/bg1.png';
import bg2 from '../../assets/img/3ds/bg2.png';
import bg3 from '../../assets/img/3ds/bg3.png';
import { Link } from 'react-router-dom';
import OpenButton from '../Utils/OpenButton';

export default function Intro() {
   return (
      <Section class="intro">
         <div className="intro__left">
            <Title class="intro__title">CIXDY</Title>
            <Text class="intro__text">
               Lorem ipsum dolor sit amet consectetur adipisicing elit. Expedita
               optio ratione eligendi quaerat amet dolor dicta aspernatur fugiat
               voluptas non!
            </Text>
            <img className="intro__img  intro__img-1" src={bg1} alt="bg1" />
            <img className="intro__img intro__img-2" src={bg2} alt="bg2" />
         </div>
         <Link to="/story" className="intro__right">
            <Title class="intro__title">
               The <br />
               story <br />
               of CIXDY
            </Title>
            <OpenButton />
            <img className="intro__img  intro__img-3" src={bg3} alt="bg3" />
         </Link>
      </Section>
   );
}
