import React, { useEffect } from 'react';

export default function Technologies() {
   useEffect(() => {
      window.addEventListener('scroll', handleScroll); // Adaugă un eveniment de ascultare pentru scroll
      return () => window.removeEventListener('scroll', handleScroll); // Elimină evenimentul de ascultare la demontare
   }, []);

   function handleScroll() {
      const firstScrollingText = document.getElementById(
         'first-scrolling-text'
      );
      const secondScrollingText = document.getElementById(
         'second-scrolling-text'
      );

      if (firstScrollingText && secondScrollingText) {
         const scrollPercentage =
            window.scrollY / (document.body.scrollHeight - window.innerHeight);
         const speed = window.innerWidth <= 800 ? 1.5 : 0.2;
         // Mută primul rând de text spre stânga
         firstScrollingText.style.transform = `translateX(-${
            scrollPercentage * 50 * speed
         }vw)`;
         // Mută al doilea rând de text spre dreapta
         secondScrollingText.style.transform = `translateX(${
            scrollPercentage * 50 * speed
         }vw)`;
      }
   }

   return (
      <section className="technologies">
         <div className="container">
            <div className="technologies__body">
               <div className="technologies__wrapper ">
                  <ul
                     className="technologies__row technologies__row-1"
                     id="first-scrolling-text"
                  >
                     <li className="technologies__item">HTML</li>
                     <li className="technologies__item">CSS</li>
                     <li className="technologies__item">SCSS</li>
                     <li className="technologies__item">Java script</li>
                     <li className="technologies__item">React</li>
                     <li className="technologies__item">Redux</li>
                     <li className="technologies__item">npm</li>
                     <li className="technologies__item">git</li>
                     <li className="technologies__item">github</li>
                     <li className="technologies__item">webpack</li>
                     <li className="technologies__item">json</li>
                     <li className="technologies__item">node</li>
                     <li className="technologies__item">HTML</li>
                     <li className="technologies__item">CSS</li>
                     <li className="technologies__item">SCSS</li>
                     <li className="technologies__item">Java script</li>
                     <li className="technologies__item">React</li>
                     <li className="technologies__item">Redux</li>
                     <li className="technologies__item">npm</li>
                     <li className="technologies__item">git</li>
                     <li className="technologies__item">github</li>
                     <li className="technologies__item">webpack</li>
                     <li className="technologies__item">json</li>
                     <li className="technologies__item">node</li>
                  </ul>
               </div>
               <div className="technologies__wrapper ">
                  <ul
                     className="technologies__row technologies__row-2"
                     id="second-scrolling-text"
                  >
                     <li className="technologies__item">figma</li>
                     <li className="technologies__item">phoroshop</li>
                     <li className="technologies__item">aftereffects</li>
                     <li className="technologies__item">premierpro</li>
                     <li className="technologies__item">indesign</li>
                     <li className="technologies__item">Illustrator</li>
                     <li className="technologies__item">Premiere Pro</li>
                     <li className="technologies__item">Affinity</li>
                     <li className="technologies__item">figma</li>
                     <li className="technologies__item">phoroshop</li>
                     <li className="technologies__item">aftereffects</li>
                     <li className="technologies__item">premierpro</li>
                     <li className="technologies__item">indesign</li>
                     <li className="technologies__item">Illustrator</li>
                     <li className="technologies__item">Premiere Pro</li>
                     <li className="technologies__item">Affinity</li>
                  </ul>
               </div>
            </div>
         </div>
      </section>
   );
}
